@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);

.headerMain {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #032047;

}

.headerTitle {
    color: #FB8500;
    margin: 20px 60px;
    font-size: 40px;
    letter-spacing: 2px;
}

.addButton {
    background-color: #77dd77;
    padding: 10px 11px;
    margin:20px 30px;
    border-radius: 100px;
    border: none;
    cursor: pointer;
    transition: 0.5s;
}
.addButton:hover {
    background-color: #b6e9b6;
    transition: 0.5s;

}
.addIcon  path{
    stroke: rgb(0, 0, 0);
}
.deleteButton {
    background-color: #FF0000;
    padding: 10px 11px;
    margin:20px 30px;
    border-radius: 100px;
    border: none;
    cursor: pointer;
    transition: 0.5s;
}
.deleteButton:hover {
    background-color: #ec9595;
    transition: 0.5s;

}


/* apply on page load  */
.closeAddPageInit{
    display: none;
}
/* apply when page is inactive  */
.closeAddPage {
    flex-direction: column;
    z-index: 1;
    position: fixed;
    max-width: 100%;
    border: none;
    border-radius: 30px;
    background-color: #FB8500;
    margin: 5% 40%;
    padding:40px 50px ;
    -webkit-animation: scale-out-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: scale-out-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
/* apply when page is active  */
.openAddPage {
    display: flex;
    z-index: 1;
    flex-direction: column;
    position: fixed;
    max-width: 100%;
    border: none;
    border-radius: 30px;
    background-color: #FB8500;
    margin: 5% 40%;
    padding:40px 50px ;
    -webkit-animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@media (max-width:700px) {
  .openAddPage {
    margin: 10%;
    padding: 30px ;
    max-width: 80%;
  }
}
/* top part  */
.addPageHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 15px;
}
/* close button  */
.addPageCloseBtn {
  border: none;
  background-color: #FB8500;
  color: grey;
  cursor: pointer;
  font-size: 16px;
}
/* form styles  */
.addForm {
    display: flex;
    flex-direction: column;
}
/* input styles  */
.addFormInputs {
    margin: 10px 0;
    padding: 10px;
    background-color: #efe0b8;
    border: none;
    border-radius: 10px;
}
.addFormInputs:focus {
    outline: none;
}
/* submit button  */
.addFormBtn {
    border: none;
    padding: 10px 0;
    background-color: #77dd77;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.5s;
}
.addFormBtn:hover {
  background-color: #b6e9b6 ;
  transition: 0.5s;
}



/* keyframes  */
@-webkit-keyframes scale-in-center {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
    }
  }
@keyframes scale-in-center {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
    }
  }
  @-webkit-keyframes scale-out-center {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
  }
  @keyframes scale-out-center {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
  }
  
  

.postsSection {
    display: flex;
    flex-wrap: wrap;
    margin: 40px;

}
.eachPost {
    margin: 20px;
    padding: 20px;
    background-color: #f4b266 ;
    border: none;
    border-radius: 10px;
    max-width: 100%;
    -webkit-animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@media (max-width:900px) {
  .postsSection {
    margin: 10px;
  }
  .eachPost {
    margin: 10px;
    padding: 10px;
  }
}
.eachPost p, .eachPost h3{
  background-color: #f4b266;
  color: #3a2002;
  border:none ;
  border-radius: 10px;
  padding: 5px 10px;
  margin-bottom: 10px;
}
.eachPost p {
  color:#743f02 ;
}
/* delete each post  */
.deletePostBtn path{
  cursor: pointer;
  transition: 0.5s;
}
.deletePostBtn:hover path{
  color: #f4b266;
  cursor: pointer;
  transition: 0.5s;
}

/* on delete  */
.shake h3 {
  margin-bottom: 10px;
}
.shake {
  -webkit-animation: vibrate-1 1s linear infinite both;
          animation: vibrate-1 1s linear infinite both;
  padding-bottom: 0;
}
/* on delete, remove delete buttons  */
.removeDeleteBtnSectionInit {
  display: none;
}
.removeDeleteBtnSection {
  -webkit-animation: slide-out-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: slide-out-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

}
.removeDeleteConfirmBtn, .removeDeleteCancelBtn {
  display: none;
  margin:20px  ;
  padding: 15px 20px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  transition: 0.5s;
}
.removeDeleteConfirmBtn {
  background-color:#ff0000 ;
}
/* on delete, show delete buttons  */
.deleteBtnSection {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: #032047;
  -webkit-animation: slide-in-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: slide-in-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.deleteConfirmBtn, .deleteCancelBtn {
  margin:20px 50px  ;
  padding: 15px 20px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: 0.5s;
}
.deleteConfirmBtn {
  background-color:#ff0000 ;
}
.deleteConfirmBtn:hover  {
  background-color: #f98080;
}
.deleteCancelBtn:hover {
  background-color: #9ad9e9;
}


/* keyframes  */

 
@-webkit-keyframes vibrate-1 {
  0% {
            -webkit-transform: translate(0);
                    transform: translate(0);
  }
  20% {
            -webkit-transform: translate(-2px, 2px);
                    transform: translate(-2px, 2px);
  }
  40% {
            -webkit-transform: translate(-2px, -2px);
                    transform: translate(-2px, -2px);
  }
  60% {
            -webkit-transform: translate(2px, 2px);
                    transform: translate(2px, 2px);
  }
  80% {
            -webkit-transform: translate(2px, -2px);
                    transform: translate(2px, -2px);
  }
  100% {
            -webkit-transform: translate(0);
                    transform: translate(0);
  }
}

 
@keyframes vibrate-1 {
  0% {
            -webkit-transform: translate(0);
                    transform: translate(0);
  }
  20% {
            -webkit-transform: translate(-2px, 2px);
                    transform: translate(-2px, 2px);
  }
  40% {
            -webkit-transform: translate(-2px, -2px);
                    transform: translate(-2px, -2px);
  }
  60% {
            -webkit-transform: translate(2px, 2px);
                    transform: translate(2px, 2px);
  }
  80% {
            -webkit-transform: translate(2px, -2px);
                    transform: translate(2px, -2px);
  }
  100% {
            -webkit-transform: translate(0);
                    transform: translate(0);
  }
}

@-webkit-keyframes slide-in-top {
  0% {
            -webkit-transform: translateY(-1000px);
                    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
            -webkit-transform: translateY(0);
                    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slide-in-top {
  0% {
            -webkit-transform: translateY(-1000px);
                    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
            -webkit-transform: translateY(0);
                    transform: translateY(0);
    opacity: 1;
  }
}
@-webkit-keyframes slide-out-top {
  0% {
            -webkit-transform: translateY(0);
                    transform: translateY(0);
    opacity: 1;
  }
  100% {
            -webkit-transform: translateY(-1000px);
                    transform: translateY(-1000px);
    opacity: 0;
    
  }
}
@keyframes slide-out-top {
  0% {
            -webkit-transform: translateY(0);
                    transform: translateY(0);
    opacity: 1;
  }
  100% {
            -webkit-transform: translateY(-1000px);
                    transform: translateY(-1000px);
    opacity: 0;
    
  }
}

  
  
  /* checkbox  */
  .hideCheckbox {
    display: none;
  }
  .showCheckbox{
    display: block;
    transition: 0.5s;
  }
  .checkboxLabel {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 20px;
    cursor: pointer;
    font-size: 25px;
  }
  
  /* Hide the default checkbox */
  .checkboxLabel input {
    visibility: hidden;
    cursor: pointer;
  }
  
  /* Create a custom checkbox */
  .newCheckbox {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #f1e4c3;
    border-radius: 20px;
    border: none;
  }
  
  .checkboxLabel:hover input ~ .newCheckbox {
    background-color: #fdd777;
  }
  
  .checkboxLabel input:checked ~ .newCheckbox {
    background-color: #ba8604;
  }
  
  /* Create the newCheckbox/indicator (hidden when not checked) */
  .newCheckbox:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the newCheckbox when checked */
  .checkboxLabel input:checked ~ .newCheckbox:after {
    display: block;
  }
  
  /* Style the newCheckbox/indicator */
  .checkboxLabel .newCheckbox:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  
